export default {
	data() {
		return {
		}
	},
	onLoad() {
	},
	methods: {
		// 前往硬币皮肤设置
		toCoinSkin () {
			uni.navigateTo({
				url: '/pages/setting/coinskin'
			});
		},
		// 前往硬币音效设置
		toCoinAudio () {
			uni.navigateTo({
				url: '/pages/setting/coinaudio'
			});
		},
		// 前往背景皮肤设置
		toCoinBg () {
			uni.navigateTo({
				url: '/pages/setting/bgCover'
			});
		},
		// 清除记录
		clearRecord () {
			uni.showModal({
			    title: '',
			    content: '是否立即清空记录',
				confirmColor: '#fd746c',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						uni.clearStorage();
						uni.showToast({
							title: '已清空',
							duration: 2000
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
